body {
  margin: 0;
  padding: 0;
  
  background-color: #efefef;

  overflow-x: hidden;
}

* {
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 200;
}

a {
  text-decoration: none;
  background-color: #333;
  color: #fefefe;
  margin: 5px 10px;
  padding: 7px 15px;
  border-radius: 5px;
  -webkit-transition: 0.3s all;
  -o-transition: 0.3s all;
  transition: 0.3s all;
}

a:hover {
  background-color: #111;
}

/* FLEX */
.flex {
  display: -ms-flexbox;
  display: flex;
}

.horizontal {
  -ms-flex-direction: row;
      flex-direction: row;
}

.vertical {
  -ms-flex-direction: column;
      flex-direction: column;
}

.flex-1 {
  -ms-flex: 1 1;
      flex: 1 1;
}

.flex-center {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

/* TEXT */

.text-center{
  text-align: center;
}

input, textarea {
  margin: 0 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 7px 12px; 
  font-size: 1rem;
}

textarea {
  width: 95%;
  resize: none;
}

/* COMPONENT */

.Header {
  height: 60px;
  border: 1px solid #999;
  margin: -1px -5px;
  background-color: #dedede;
  -webkit-box-shadow: 1px 1px 8px;
          box-shadow: 1px 1px 8px;
}

.Header > div:hover {
  background-color: #eee;
}

.Header > div {
  border-right: 1px #999 solid;
}

.Header > .selected {
  background-color: #eee;
}

.content {
  margin: 20px auto;
  width: 100%;
  max-width: 720px;
}

.Aluguel, .Whatsapp {
  padding: 10px 30px;
}

.Footer {
  border-top: 1px solid #aaa;
  padding-top: 10px 
}

/* OPCOES */

strong {
  font-weight: 600;
}

.subl {
  border-bottom: 2px #222 solid;
}

.pointer {
  cursor: pointer;
}




